@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular..woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Semibold.woff2") format("woff2"), url("../fonts/Montserrat-Semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #f9fbfd;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 14px 40px;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.header {
  background-color: rgba(255, 255, 255, 0);
  min-height: 100px;
  z-index: 10;
}

.header .container {
  padding-top: 35px;
  display: flex;
  justify-content: space-between;
}

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  padding-top: 8px;
}

.nav__item:not(:last-child) {
  margin-right: 33px;
}

.nav__link {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: #f9fbfd;
}

.intro {
  background: url("../../img/intro-desk.jpg") center no-repeat;
  background-size: cover;
  min-width: 100%;
  margin-top: -100px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .intro {
    background: url("../../img/intro-desk@2x.jpg") center no-repeat;
    background-size: cover;
  }
}

.intro .container {
  padding-top: 271px;
  padding-bottom: 285px;
}

.intro h1 {
  margin: 0;
  padding: 0;
  padding-left: 95px;
  margin-bottom: 11px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 54px;
  line-height: 1.22;
  color: #f9fbfd;
}

.intro p {
  margin: 0;
  padding: 0;
  padding-left: 95px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  color: #f9fbfd;
}

.description {
  background: url("../../img/penguins.jpg") 87.2% 101% no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .description {
    background: url("../../img/penguins@2x.jpg") 87.2% 44% no-repeat;
    background-size: cover;
  }
}

.description .container {
  padding-top: 169px;
  padding-bottom: 42px;
  margin-bottom: 146px;
}

.description h2 {
  margin: 0;
  padding: 0;
  margin-bottom: 24px;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 1.5;
  color: #011c40;
}

.description p {
  max-width: 512px;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #011c40;
}

.advantages {
  background: url("../../img/advantages-bg-desk.jpg") center no-repeat;
  background-size: cover;
  min-width: 100%;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .advantages {
    background: url("../../img/advantages-bg-desk@2x.jpg") center no-repeat;
    background-size: cover;
  }
}

.advantages .container {
  padding-top: 110px;
  padding-bottom: 120px;
}

.advantages h2 {
  margin: 0;
  margin-bottom: 49px;
  padding: 0;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 1.5;
  color: #011c40;
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
}

.advantages__item {
  position: relative;
  background-color: #f9fbfd;
  padding: 81px 32px 46px 30px;
}
.advantages__item::before {
  position: absolute;
  top: 30px;
  left: 30px;
  height: 32px;
}
.advantages__item--ship::before {
  content: url("../../img/svg/advantages-ship.svg");
  width: 27px;
}
.advantages__item--services::before {
  content: url("../../img/svg/advantages-services.svg");
  width: 38px;
}
.advantages__item--tours::before {
  content: url("../../img/svg/advantages-tours.svg");
  width: 44px;
}

.advantages__item h3 {
  margin: 0;
  margin-bottom: 9px;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  color: #011c40;
}

.advantages__item p {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: #011c40;
}

.footer {
  background-color: #011c40;
  min-height: 138px;
}

.footer .container {
  display: flex;
  padding-top: 50px;
}

.footer__logo {
  margin-right: 40px;
}

.footer__nav {
  margin-right: auto;
}

.footer__socials {
  display: flex;
  align-items: center;
}

.footer__socials-list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.footer__socials-item:not(:last-child) {
  margin-right: 28px;
}

